<template>
  <div class="hot">
    <HomeLink></HomeLink>
    <div class="hot_bg">
      <div class="hotCon">
        <div class="hotIcon"></div>
        <div class="hotUpdate">更新日期:{{setUpdateTime()}}</div>

      </div>
    </div>
    <ul class="hotSongCon">
      <track-songs v-for="(item,index) in hotSongList" :key="index" :trackSongs="item" :order="index" @translate-song="$emit('translate-song', $event)" :news="false" :currentSong="currentSong"></track-songs>
    </ul>
  </div>
</template>

<script>
  import HomeLink from "@/components/HomeLink.vue";
  import TrackSongs from "@/components/TrackSongs.vue";
  export default {
    name: "Hot",
    components: {
      HomeLink,
      TrackSongs,
    },
    props:{
      currentSong:Object
    },
    data() {
      return {
        hotSongList: [],
        updateTime: null,
      }
    },
    methods: {
      getHotSong() {
        this.axios.get('/top/list?idx=1').then(response => {
          // console.log(response.data.playlist.tracks)
          this.hotSongList = response.data.playlist.tracks;
          this.updateTime = response.data.playlist.updateTime;
          window.localStorage.setItem('hotRankingList', JSON.stringify({
            // 过期时间 一天后
            expire: Date.now() * 24 * 60 * 60 * 1000,
            result: [...response.data.playlist.tracks].slice(0, 20),
            updateTime: response.data.playlist.updateTime
          }))
        }).catch(error => {
          console.log(error)
        })
      },
      // 转换为具体时间
      setUpdateTime() {
        // 传入时间
        let date = new Date(parseInt(this.updateTime))
        // console.log(this.updateTime);
        // 月
        let m = date.getMonth() + 1;
        m = m < 10 ? ('0' + m) : m;
        // 日
        let d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        return m + "月" + d + "日";
      }
    },

    // 创建时拿数据
    created() {
      const hasHotRankingList = JSON.parse(window.localStorage.getItem('hotRankingList'));
      if (hasHotRankingList && hasHotRankingList.expire > Date.now()) {
        this.hotSongList = hasHotRankingList.result;
        this.updateTime = hasHotRankingList.updateTime
      } else {
        this.getHotSong();
      }
    },
  };
</script>
<style lang="less" scoped>
  .hot_bg {
    background-image: url('../assets/hot_music_bg_2x.jpg');
    padding-top: 39%;
    background-size: contain;
    position: relative;

    &:after {
      content: " ";
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      background-color: rgba(0, 0, 0, 0.2);
    }

    .hotCon {
      display: flex;
      justify-content: center;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 2;
      padding-left: 20px;
      box-sizing: border-box;
      flex-direction: column;

      .hotIcon {
        width: 142px;
        height: 67px;
        background-position: -24px -30px;
        background-image: url("../assets/index_icon_2x.png");
        background-size: 166px 97px;
      }

      .hotUpdate {
        margin-top: 10px;
        font-size: 12px;
        color: white;
      }
    }
  }
</style>