<template>
  <div class="searchRes">
    <div class="res" @click="send(searchText)" v-if="showDefault">
      <p>搜索"{{searchText}}"</p>
      <ul class="associate">
        <li v-for="(item, index) in associate" :key="index" @click="send(item.keyword)"><i
            class="fa fa-search"></i>{{item.keyword}}</li>
      </ul>
    </div>
    <div v-else class="data">
      <search-songs v-for="(item, index) in dataList" :key="index" :searchSongs="item" @translate-song="currentSong=$emit"></search-songs>
    </div>
  </div>
</template>

<script>
  import SearchSongs from "@/components/SearchSongs.vue"
  export default {
    name: "SearchRes",
    components: {
      SearchSongs
    },
    props: {
      searchText: String,
      associate: Array,
      state: Boolean
    },
    data() {
      return {
        showDefault: true,
        dataList: [],
        currentSong:null,
      }
    },
    methods: {
      send(value) {
        window.axios.get('search', {
          params: {
            keywords: value,
            type: 1
          }
        }).then(response => {
          if (response) {
            // console.log(response);
            this.showDefault = false;
            this.dataList = response.data.result.songs
          }
        })
      }
    },
    watch: {
      searchText:function(){
        this.showDefault = true;
      }
    },
  }
</script>

<style lang="less" scoped>
  .searchRes {
    margin: 0 20px;

    .res {
      p {
        padding: 10px 0;
        border-top: 1px solid lightgray;
        border-bottom: 1px solid rgba(188, 188, 188, .4);
        color: rgb(34, 34, 238);
      }

      .associate {
        li {
          border-bottom: 1px solid rgba(188, 188, 188, .4);
          padding: 10px 0;

          i {
            margin: 0 5px;
            color: rgba(188, 188, 188, .4)
          }
        }
      }
    }
  }
</style>