import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Hot from "../views/Hot.vue";
import Search from "../views/Search.vue";
import Personalized from "../views/Personalized.vue";
Vue.use(VueRouter);

const routes = [{
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/hot",
    name: "Hot",
    component: Hot
  },
  {
    path: "/search",
    name: "Search",
    component: Search
  },
  {
    path: "/personalized",
    name: "Personalized",
    component: Personalized
  }, 
];

const router = new VueRouter({
  routes
});

export default router;