<template>
  <div class="search">
    <HomeLink>
    </HomeLink>
    <div class="searchBox">
      <i class="fa fa-search"></i>
      <input type="text" v-model="searchText" :placeholder="defaultText">
      <i class="fa fa-close" v-show="searchText!=''" @click="searchText=''"></i>
    </div>
    <div class="hotSearch" v-show="searchText.trim()==''">
      <p>热门搜索</p>
      <div class="hotSearchBox">
        <span class="hotWord" v-for="(item, index) in hotWordList" :key="index"
          @click="send(item.first)">{{item.first}}</span>
      </div>
    </div>
    <SearchRes v-show="searchText.trim()!=''" :searchText='searchText' :associate="associate" ></SearchRes>
  </div>
</template>

<script>
  import HomeLink from "@/components/HomeLink.vue";
  import SearchRes from "@/components/SearchRes.vue";
  export default {
    name: "Search",
    components: {
      HomeLink,
      SearchRes
    },
    props:{
      currentSong:Object
    },
    data() {
      return {
        searchText: "",
        defaultText: "搜索歌曲...",
        hotWordList: [],
        associate: null,
      }
    },
    methods: {
      getSearchDefault() {
        window.axios.get('search/default').then(response => {
          // console.log(response.data);
          this.defaultText = response.data.data.showKeyword;
        })
      },
      getSearchHot() {
        let hasSearchList = JSON.parse(window.localStorage.getItem('searchHotWord'));
        if (hasSearchList && hasSearchList.expire > Date.now()) {
          this.hotWordList = hasSearchList.result;
        } else {
          window.axios.get('search/hot').then(response => {
            // console.log(response.data.result.hots);
            this.hotWordList = response.data.result.hots;
            window.localStorage.setItem('searchHotWord', JSON.stringify({
              expire: Date.now() + 6 * 60 * 60 * 1000,
              result: response.data.result.hots,
            }));
          })
        }
      },
      send(value) {
        window.axios.get('search', {
          params: {
            keywords: value,
            type: 1
          }
        }).then(response => {
          if (response) {
          this.searchText=value;
          this.dataList = response.data.result.songs
          }
        })
      },
    },

    created() {
      this.getSearchDefault();
      this.getSearchHot();
    },
    watch: {
      searchText: function (value) {
        if (value.trim() != 0) {
          window.axios.get('search/suggest', {
            params: {
              keywords: value,
              type:'mobile'
            },
          }).then(response => {
            // console.log(response.data.result.allMatch);
            this.associate = response.data.result.allMatch
          })
        }
      }
    },
  };
</script>

<style lang="less" scoped>
  .search {
    .searchBox {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      position: relative;
      background-color: #ebecec;
      width: 90%;
      margin: 20px;
      border-radius: 20px;
      height: 2em;

      i {
        margin: 0 10px;
        color: lightgray;
        font-size: 14px;
      }

      input {
        background: none;
        outline: none;
        border: none;
        color: black;
        font-size: 16px;
        flex: 1;

        &::-webkit-input-placeholder {
          color: lightgray;
        }

      }
    }

    .hotSearch {
      border-top: 1px solid lightgray;
      padding: 10px 5px;

      p {
        font-size: 12px
      }

      .hotSearchBox {
        margin-top: 5px;

        span {
          display: inline-block;
          border-radius: 15px;
          border: 1px solid lightgray;
          padding: 7px 10px;
          line-height: 1em;
          margin: 5px 5px;
          font-size: 14px;
        }
      }
    }
  }
</style>