<template>
  <div id="app" @touchstart="touchstart" @touchend="touchend"
    :style="{'padding-bottom':currentSong!=null?'30px':'0px'}">
    <!-- 一级路由出口 -->
    <keep-alive>
      <router-view @translate-song="currentSong = $event" :currentSong="currentSong" @showLoading="showLoading=$event">
      </router-view>
    </keep-alive>
    <!-- 播放器 -->
    <PlaySongList @translate-song="currentSong = $event" v-if="currentSong" :currentSong="currentSong"></PlaySongList>
    <loading :showLoading="this.$store.state.loading" class="loading"></loading>
  </div>
</template>
<script>
  import PlaySongList from "@/components/PlaySongList.vue";
  import Loading from "@/components/Loading.vue";
  export default {
    components: {
      PlaySongList,
      Loading
    },
    data: function () {
      return {
        currentSong: null,
        touchstartX: 0,
        showSongDetail: false,
      };
    },
    methods: {
      touchstart: function (event) {
        // console.log("开始", event.changedTouches[0].clientX);
        this.touchstartX = event.changedTouches[0].clientX;
        this.touchstartY = event.changedTouches[0].clientY;
      },
      touchend: function (event) {
        // console.log("结束", event.changedTouches[0].clientX);
        // 判断左滑动 返回上一页
        if (event.changedTouches[0].clientX - this.touchstartX > 80) {
          // console.log("从左到右");
          this.touchstartX = 0;
          this.touchstartY = 0;
          this.routerBack();
        }
        // 右滑动 
        if (event.changedTouches[0].clientX - this.touchstartX < -80) {
          // console.log('右到左');
          this.touchstartX = 0;
          this.touchstartY = 0;
          this.routerForward();
        }
      },
      // 回退方法  相当于history.go(-1)
      routerBack: function () {
        this.$router.back();
      },
      // 前进方法  相当于history.go(1)
      routerForward: function () {
        this.$router.forward();
      },
    },
  }
</script>
<style lang="less">
  #app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;

    .loading {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      background: rgba(0, 0, 0, 0.4);
      display: flex;
      z-index: 999;
      justify-content: center;
      align-items: center;

      img {
        width: 36px;
      }
    }
  }
</style>