<template>
  <div class="playlist">
    <template v-if="songListDetail">
      <header>
        <!--背景遮罩层 -->
        <div class="mask" :style="{backgroundImage: `url(${songListDetail.coverImgUrl})`}"></div>
        <!-- 左边 -->
        <div class="lt">
          <img :src="songListDetail.coverImgUrl" alt />
          <span class="playCount">
            <i class="fa fa-headphones"></i>
            {{returnCount(songListDetail.playCount)}}
          </span>
          <span class="icon">歌单</span>
        </div>
        <!-- 右边 -->
        <div class="rt">
          <h3>{{songListDetail.name}}</h3>
          <img :src="songListDetail.creator.avatarUrl" alt />
          <span>{{songListDetail.creator.nickname}}</span>
        </div>
      </header>
      <main>
        <div class="tag">
          <span>标签：</span><i v-for="(item,index) in songListDetail.tags" :key="index">{{item}}</i>
        </div>
        <div class="abstract" :class="{show: showMoreDesc}">
          <span>简介：</span><span>{{songListDetail.description}}</span>
        </div>
        <div class="more" @click="showMoreDesc = !showMoreDesc">
          <i v-if="showMoreDesc" class="fa fa-angle-up"></i>
          <i v-else class="fa fa-angle-down"></i>
        </div>
      </main>
       <h3 class="allSongList">歌曲列表</h3>
      <track-songs v-for="(item,index) in trackSongList" :key='index' :order = "index" :trackSongs="item" @translate-song="$emit('translate-song', $event)" :news="false" :currentSong="currentSong"></track-songs>
    </template>
  </div>
</template>

<script>
import TrackSongs from "@/components/TrackSongs.vue"
  export default {
    name: "Personalized",
    components:{
      TrackSongs
    },
    data() {
      return {
        songListId: this.$route.query.id,
        songListDetail: null,
        showMoreDesc: false,
        trackSongList:[]
      }
    },
    props:{
      currentSong:Object
    },
    methods: {
      returnCount(n) {
        // 播放量
        if (n > 100000000) {
          return (n / 100000000).toFixed(1) + '亿';
        } else if (n > 10000) {
          return (n / 10000).toFixed(1) + '万';
        } else {
          return n
        }
      },
    },
    // 第一次进入时
    // created() {
    //   //   console.log(this.$data);
    //   // 本地存储的数据是否存在或过期判断
    beforeRouteEnter(to, from, next) {
      // 传next回调 进行本组件实例访问,大排行确认时执行回调,组件实例作为参数,修改后的数据替代组件实例的数据
      // console.log(this)
      const hasSongListDetail = JSON.parse(window.localStorage.getItem('sl-' + to.query.id));
      if (hasSongListDetail && hasSongListDetail.expire > Date.now()) {
        // 未过期
        next((vm) => {
          // 通过'vm' 访问该组件实例
          vm.songListId = Number(to.query.id);
          vm.songListDetail = hasSongListDetail.result
          vm.trackSongList = hasSongListDetail.tracks
        })
      } else {
        // 过期了
        window.axios.get('playlist/detail', {
          params: {
            id: to.query.id
          }
        }).then(response => {
          // console.log(response);
          // 数据正确传入到空数组中
          let playlist = response.data.playlist;
          let tracks= response.data.playlist.tracks;

          // 本地存储数据 减少调用
          window.localStorage.setItem('sl-' + to.query.id,
            JSON.stringify({
              expire: Date.now() + 1 * 60 * 60 * 1000,
              result:response.data.playlist,
              tracks:response.data.playlist.tracks
            })
          );
          next(vm => {
            // 通过 `vm` 访问组件实例
            vm.songListId = Number(to.query.id);
            vm.songListDetail = playlist;
            vm.trackSongList =tracks
          })
        }).catch(error => {
          console.log(error);
        })
        // 守卫回调
        // 守卫回调
      }
    }
    // 获取数据
    // // 第一次进入之后需要监听路由
    // // 路由更新 守卫  进入之前 拦截路由
    // beforeRouteUpdate(to, from, next) {
    //   // next 一定要调用函数
    //   console.log(to, from);
    //   from;
    //   this.songListId = Number(to.query.id);
    //   next();
    // }
  }
</script>

<style lang="less" scoped>
  img {
    max-width: 100%;
  }

  header {
    display: flex;
    padding: 30px 15px;
    position: relative;
    overflow: hidden;
    color: white;

    .mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      filter: blur(30px) brightness(0.8);
      transform: scale(1.5);
      background-position: center;
    }

    .lt {
      flex: 4;
      position: relative;
      font-size: 12px;
      line-height: 18px;

      .xxx() {
        position: absolute;
        top: 0;
        left: 0;
      }

      .playCount {
        .xxx();
        width: 100%;
        text-align: right;
        padding: 0 5px;
        background: rgba(0, 0, 0, 0.3);
      }

      .icon {
        .xxx();
        top: 15px;
        background: #bb3b3f;
        padding: 0 10px;
        border-top-right-radius: 1em;
        border-bottom-right-radius: 1em;
        display: inline;
      }
    }

    .rt {
      flex: 6;
      padding-left: 15px;

      h3 {
        padding: 5px 0 20px 0;
      }

      img {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        vertical-align: middle;
      }

      span {
        font-size: 14px;
        padding-left: 15px;
      }
    }
  }

  main {
    padding: 0 10px;
    line-height: 1.5em;

    .tag {
      margin: 10px 0;

      span {
        vertical-align: middle;
      }

      i {
        font-size: 12px;
        margin-right: 5px;
        font-style: normal;
        border: 1px solid lightgray;
        border-radius: 12px;
        padding: 2px 5px;
      }
    }

    .abstract {
      height: 4.5em;
      overflow: hidden;
      text-overflow: ellipsis;

      &.show {
        height: auto;
      }

      span {
        white-space: pre-wrap;
        vertical-align: baseline;
      }
    }
    .more {
      overflow: hidden;
      text-align: right;
      font-size: 16px;
    }
    
  }
  .allSongList{
      background-color: #eeeff0;
      font-size: 14px;
      text-indent: .5em;
    }
</style>