<template>
    <li class="card" @click="detailList(songList.id)">
        <div class="pic">
            <img :src="songList.picUrl" alt="">
            <span><i class="fa fa-headphones"></i>{{returnCount(songList.playCount)}}</span>
        </div>
        <h5>{{songList.name}}</h5>
    </li>
</template>

<script>
    export default {
        name: "MusicList",
        props: {
            songList: {
                type: Object,
                // 默认数据
                default: () => {
                    return {
                        id: 4969079749,
                        type: 0,
                        name: "欧美 | 欢快小调拯救你的不开心",
                        copywriter: "编辑推荐：音乐是治愈人心的良药",
                        picUrl: "https://p2.music.126.net/nflx5ZxSSPivvGnyS5WRGw==/109951164906376339.jpg",
                        canDislike: false,
                        trackNumberUpdateTime: 1586934907095,
                        playCount: 1209355,
                        trackCount: 100,
                        highQuality: false,
                        alg: "featured"
                    };
                }
            }
        },
        methods: {
            //播放量修改
            returnCount(n) {
                if (n > 100000000) {
                    return (n / 100000000).toFixed(1) + '亿';
                } else if (n > 10000) {
                    return (n / 10000).toFixed(1) + '万';
                } else {
                    return n
                }
            },
            // 路由导航 =>详细界面
            detailList(id) {
                this.$router.push({
                    path: "personalized",
                    query: {
                        id: id
                    }
                })
            }
        },
    }
</script>

<style lang="less" scoped>
    .card {
        flex-basis: 30%;
        flex-grow: 1;
        margin-bottom: 5px;
        cursor: pointer;

        &:nth-child(3n + 2) {
            margin: 0 3px;
        }

        .pic {
            position: relative;

            img {
                vertical-align: middle;
                width: 100%;
            }

            span {
                position: absolute;
                top: 0;
                right: 0;
                color: white;
                i{
                    margin-right: 3px;
                }
            }
        }

        h5 {
            font-size: 13px;
            margin: 3px 0 5px 0;
            line-height: 1.2em;
            height: 2.5em;
            overflow: hidden;
            font-weight: 500;
            padding: 3px 5px;
            box-sizing: border-box;
            display: inline-block;
        }
    }
</style>