<script>
    export default {
        name: 'SearchSongs',
        props: {
            searchSongs: Object,
        },
        methods: {
            // 艺术家/
            artistConnect(artists) {
                return artists.map(artists => artists.name).join(" / ");
            },
        },
    }
</script>

