<template>
  <div class="home">
    <HomeLink></HomeLink>
    <div class="personalized">
      <HomeTitle>推荐歌单</HomeTitle>
      <ul class="showPersonalized">
        <MusicList v-for="(item,index) in showMusicList()" :key="index" :songList="item"></MusicList>
      </ul>
    </div>
    <ul class="recommend">
      <HomeTitle>推荐音乐</HomeTitle>
      <track-songs v-for="(item,index) in newSongList" :key="index" :trackSongs="item" :news="true"
        @translate-song="$emit('translate-song', $event)" :currentSong="currentSong"></track-songs>
    </ul>
  </div>
</template>

<script>
  // @ is an alias to /src
  import HomeLink from "@/components/HomeLink.vue";
  import HomeTitle from "@/components/HomeTitle.vue";
  import MusicList from "@/components/MusicList.vue";
  import TrackSongs from "@/components/TrackSongs.vue";
  export default {
    name: "Home",
    components: {
      HomeLink,
      HomeTitle,
      MusicList,
      TrackSongs,
    },
    props: {
      currentSong: Object
    },
    data() {
      return {
        musicList: [],
        newSongList: [],
      }
    },
    methods: {
      getPersonalized() {
        // 获取api中的推荐歌单 
        window.axios.get("/personalized").then((response) => {
          // console.log(response);
          this.musicList = response.data.result;
          // 存入到localstorage
          // window.localStorage.setItem(key,value)
          window.localStorage.setItem("personalized", JSON.stringify({
            // 销毁时间  规定在一个小时后
            expire: Date.now() * 1 * 60 * 60 * 1000,
            // 数据 =>字符串传入
            result: response.data.result
          }))
        }).catch(function (error) {
          console.log(error);
        });
      },
      getNewSong() {
        this.axios.get('/personalized/newsong').then(response => {
          // console.log(response.data.result);
          this.newSongList = response.data.result;
          // 数据存入到localstorage中
          window.localStorage.setItem('newSongList', JSON.stringify({
            // 每隔12小时获取新歌曲
            expire: Date.now() + 12 * 60 * 60 * 1000,
            result: this.newSongList
          }))
        }).catch(error => {
          console.log(error);
        })
      },
      // 截取前六位的歌单数据
      showMusicList() {
        // 深拷贝[...array]
        return [...this.musicList].slice(0, 6);
      },
    },
    // 每次创建 调用推荐歌单
    created() {
      // 推荐歌单
      const haspersonalized = JSON.parse(
        window.localStorage.getItem("personalized")
      );
      // 如果数据存在且没有过期
      if (haspersonalized && haspersonalized.expire > Date.now()) {
        this.musicList = haspersonalized.result;
      } else {
        this.getPersonalized()
      }
      // 新歌曲
      const hasNewSongList = JSON.parse(window.localStorage.getItem('newSongList'));
      // 如果存在且没超出时间 就继续调用本数据
      if (hasNewSongList && hasNewSongList.expire > Date.now()) {
        this.newSongList = hasNewSongList.result;
      } else {
        // 重新获取数据
        this.getNewSong();
      }
    },
    // 路由守卫
    // beforeRouteEnter(to, from, next) {
    //   next(vm => {
    //     vm.showLoading=false;
    //   })
    // },
    // beforeRouteLeave(to, from, next) {
    //   // 离开路由调用
    //   // 可以通过 this 访问组件
    //   // console.log(this);
    //   this.showLoading=true;
    //   next();
    // }
  };
</script>
<style lang="less" scoped>
  .home {
    position: relative;

    .showPersonalized {
      display: flex;
      flex-wrap: wrap;
    }
  }
</style>